import React, { Component } from 'react'
import Scroll from './Scroll'

import { ReactComponent as NavLogo } from '../assets/img/logos/logo-transparent-no-text.svg'

export default class Header extends Component {
  constructor (props) {
    super(props)
    this.state = {
      openMenu: false,
      visibilityClass: ''
    }
  }

  toggleMenu = value => {
    this.setState({ openMenu: value })
  };

  handleScroll = () => {
    const { visibilityClass } = this.state
    if (window.pageYOffset > 300) {
      if (visibilityClass !== 'navbar-shrink shadow-sm')
        this.setState({ visibilityClass: 'navbar-shrink shadow-sm' })
    } else {
      if (visibilityClass === 'navbar-shrink shadow-sm')
        this.setState({ visibilityClass: '' })
    }
  };

  componentDidMount () {
    window.addEventListener('scroll', this.handleScroll)
  }

  componentWillUnmount () {
    window.removeEventListener('scroll', this.handleScroll)
  }

  render () {
    const { openMenu, visibilityClass } = this.state
    return (
      <nav
        className={`navbar navbar-expand-lg fixed-top ${visibilityClass}`}
        id="mainNav"
      >
        <div className="container">
          <Scroll type="class" element="page-top">
            <a className="navbar-brand" href="#page-top" aria-label="Scroll to top">
              <NavLogo />
            </a>
          </Scroll>
          <button
            onClick={_ => this.toggleMenu(!openMenu)}
            className={`navbar-toggler navbar-toggler-right ${
              openMenu ? '' : 'collapsed'
            }`}
            type="button"
            aria-controls="navbarResponsive"
            aria-expanded={openMenu}
            aria-label="Toggle navigation"
          >
            Menu
            <i className="fas fa-bars"></i>
          </button>
          <div
            className={`collapse navbar-collapse animate slideIn ${openMenu ? 'show' : ''}`}
            id="navbarResponsive"
          >
            <ul className="navbar-nav ml-auto">
              <li className="nav-item">
                <Scroll
                  onClick={_ => this.toggleMenu(!openMenu)}
                  type="id"
                  element="about"
                >
                  <a className="nav-link" href="#about">
                    About
                  </a>
                </Scroll>
              </li>
              <li className="nav-item">
                <Scroll
                  onClick={_ => this.toggleMenu(!openMenu)}
                  type="id"
                  element="contact"
                >
                  <a className="nav-link" href="#contact">
                    Contact
                  </a>
                </Scroll>
              </li>
            </ul>
          </div>
        </div>
      </nav>
    )
  }
}
